import UserController from '../controllers/UserController';

Cake.create('login','#login',{
    root:'#app',
    data(){
        this.superadmin = {
            username:'superadmin',
            password:'123456',
            role:'superadmin'
        };
    },
    handlers:{
        async destroy(){
            await this.reset();
        },
        async submit(e){
            let json = this.utils.toFormData(e.target,{json:true});

            let {username, password} = json;




            await this.fire('spin','login');

            try {
                let user = await UserController.get({username:username});
                // user = await user.json();


                if(user && user.length){
                    this.fire('spinout','login');

                    if(user[0].status != 'active'){
                        this.fire('error','account is inactive');
                        return;
                    };
                    let $this= this;
                    if(user[0].password == password){
                        await this.fire('success','login successful');

                        await this.$router.login({
                            token:moment().format(),
                            role:user[0].role,
                            data:user[0],
                        });

                    } else {
                        this.fire('error','wrong password');
                    };
                } else {
                    this.fire('spinout','login');
                    this.fire('error','user not found');
                };
            } catch(err){
                this.fire('spinout','login');
                this.fire('error',err.message);
            }
        },

    },
    subscribe:{

    },
});