Cake.create('toolbar','#toolbar',{
    root:'.toolbar-container',
    handlers:{
        async destroy(){
            await this.reset();
        },
        async isConnected(){
            let auth = await this.fire('rolePermission');

            let toolbars = [
                {name:'sync',display:'Sync'},
            ];

            if(auth.action.includes('add')){
                toolbars.unshift({name:'add',display:'Add Record'});
            };

            this.$scope.set('toolbars',toolbars);
        },

        click(e){
            let target = e.target;
            let action = target.dataset.name;
            this.fire(action);
        }
    },
    subscribe:{}
});